
import React from 'react'
import { Card } from 'react-bootstrap'; 

import './ProductShow'

function Electronics({link1,link2,link3,link4,description}) {
    return (
        <div>
 <Card  className="varient__products"style={{  border:'2px solid silver' ,marginRight:'2rem', marginTop:'40px'}}>
 <Card.Title variant="top"  className='mx-0 my-0 px-0 py-0'>{description}</Card.Title>
  
  <Card.Body className='mx-0 my-0 px-0 py-0'>
    {
      (link2)?<div className='multi__items'>
        <div  className='item'> <Card.Img  src={link1}/></div>
        <div className='item'> <Card.Img  src={link2}/></div>
      
        <div className='item'> <Card.Img  src={link3}/></div>
        <div className='item'> <Card.Img  src={link4}/></div>
      </div>:
  <Card.Img  src={link1}/>
    }
    <Card.Text>
     see more
    </Card.Text>
  </Card.Body>
  
</Card>
        </div>
    )
}

export default Electronics
