
import { IconButton } from '@material-ui/core';
import React from 'react'
import { Navbar,Nav,Form,FormControl } from 'react-bootstrap';
import { CartPlus,Search,DoorOpen } from 'react-bootstrap-icons';

function NavbarTop() {
    return (
        <div>
   <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
    <div className="d-flex flex-grow-1">
    <Navbar.Brand href="#home"><CartPlus/>SmartCart</Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
  <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="mr-2 my-auto w-100 d-inline-block order-1">
        <Form >
        <div class="input-group custom-search">
   <FormControl type="text" class="form-control custom-search-input mx-0 px-0 px-0 py-0" placeholder="Search your Product"/>
   <button class="btn btn-warning custom-search-botton" type="submit"> <Search style={{color:"black"}}/></button>  
 </div>
        </Form>
           </Nav>
       
     
  </Navbar.Collapse>
    </div>
    <IconButton>
    <Nav.Item style={{color:'white'}}>LOGIN<DoorOpen style={{color:'white'}}/></Nav.Item>
    </IconButton>
    
</Navbar>    
        </div>
    )
}

export default NavbarTop;
