import React from 'react'
import {Carousel } from 'react-bootstrap'

import {bannerLinks} from './ImageLinks.js'
import './Banner.css'

function Banner() {
    return (
        <div className="banner_ribana">
       <Carousel>
        
{
        
       bannerLinks.map((item,i)=>{
           return(
        <Carousel.Item key={item.id}> 
       
        <img 
         className="d-block w-100 img__class"  height="220px"
          src={item.links}
          alt="First slide"
        />
        <Carousel.Caption>
         <h3 >{item.title}</h3>
   
        </Carousel.Caption>
      
             
      </Carousel.Item>
           )
            })
      
        }     
        
        </Carousel>
        </div>
    )
  
}





       
 

export default Banner
