import React from 'react'
import { Card,Button } from 'react-bootstrap'; 
import { InlineIcon } from '@iconify/react';
import currencyBdt from '@iconify-icons/mdi/currency-bdt';

function ItemCard({link,category,price,description,shops}) {
    return (
        <div>
 <Card className="bg-dark text-white custom__card" style={{  border:'2px solid silver' ,margin:'2px', overflowX:'auto'}}>
  
  <Card.Img src={link} className="card_image" height="150" style={{backgroundColor:'white'}}/>
 
 {
     (shops!==1)? <div><Card.ImgOverlay  className='mx-0 my-0 px-0 py-0 '>
     <div className="card__text" >
         <div className="left__text" >
 
             <div style={{backgroundColor: 'black', padding:'3px'}}>{category}</div>
         </div>
         <div className="right__text">
             
             <div style={{backgroundColor: 'red', padding:'3px'}}>VIEW ALL</div>
         
         </div>
     </div>
     
     </Card.ImgOverlay> <div >
    <Card.Text style={{ margin:'0px', padding:'0px', backgroundColor:'white'}} >
      <div className="bottom__text">
          <div className="left__bottomtext">
              <div style={{color:'red'}}><InlineIcon icon={currencyBdt} />{price}</div>

          </div>
          <div className="right__bottomtext">
              <div style={{color:'black'}}>{description}</div>
          </div>
      </div>
      
      <div style={{display:'flex',flex:'1',justifyContent:'center'}}>
     <Button className="mx-2 my-2 px-4 py-0" >Sign In To Add</Button></div>
     <div className="text-muted">
       <div><small>In Store</small></div>
       <div><small>Pick Up</small></div>
       <div><small>Delivery</small></div>
       <div><small>Ship</small></div>
        </div>
    </Card.Text>
   
  
        
      
      
      </div></div>:<div></div>
 }
  
   
    
    
</Card>   
        </div>
    )
}

export default ItemCard
