import React from 'react'
import ItemCard from './ItemCard'
import Electronics from './Electronics';
import Carousel from 'react-multi-carousel';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faRocket } from '@fortawesome/free-solid-svg-icons'
import 'react-multi-carousel/lib/styles.css';
import './ProductShow.css'
import {itemLinks,shopLinks,electronicLinks} from './ImageLinks'

function ProductShow() {
    const responsive = {
        superLargeDesktop: {
       
          breakpoint: { max: 4000, min: 3000 },
          items: 6
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 5
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 3
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1
        }
      };
      const responsive1 = {
        superLargeDesktop: {
       
          breakpoint: { max: 4000, min: 3000 },
          items: 9
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 7
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 4
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 2
        }
      };
      const responsive2 = {
        superLargeDesktop: {
       
          breakpoint: { max: 4000, min: 3000 },
          items: 4
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 4
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1
        }
      };
    return (
        <div className="container-fluid">
            <h4><FontAwesomeIcon  icon={faRocket} className="mx-1 mt-2" />Start Your Whole Sale Shopping</h4>
            <Carousel responsive={responsive}>
                
      {          
     itemLinks.map((item,i)=>{
           return(
       <div  key={item.id}> 
              
            <ItemCard link={item.links} category={item.category} price={item.price} description={item.description} /> 
             </div>
               )
          })
         
        }   
         
 
</Carousel>

<h4>Big Selection Stores</h4>
<Carousel responsive={responsive1}>
                
                {          
               shopLinks.map((item,i)=>{
                     return(
                 <div  key={item.id}> 
                        
                      <ItemCard link={item.links} shops={1} /> 
                       
               
                           
                      </div>
          
                     )
                      })
                   
                  }   
                   
           
          </Carousel>
          <div >
          
         <div className='container-fluid home_image'>
          <Carousel responsive={responsive2}>
                
                {          
               electronicLinks.map((item,i)=>{
                     return(
                 <div  key={item.id}> 
                        
                      <Electronics link1={item.link1} link2={item.link2} link3={item.link3} link4={item.link4} description={item.description}/> 
                       
               
                           
                      </div>
          
                     )
                      })
                   
                  }   
                   
           
          </Carousel> 
          </div>     
          </div>
</div>
    )
}

export default ProductShow
